                                <td class="nowrap-table py-1 pl-0">Status Penggajian</td>
                                <td class="nowrap-table py-1">:</td>
                                <td class="py-1 text-left font-weight-bolder" :class="modalData.employee.status_salary === 'calculated'? 'text-success': 'text-danger' "> {{ modalData.status_salary | parse('calculated') }} </td>
                              </tr>
                              <tr>
                                <td class="nowrap-table py-1 pl-0">Total Penerimaan</td>
                                <td class="nowrap-table py-1">:</td>
                                <td class="py-1 text-left"> Rp.&emsp;{{ modalData.employee.salary_value || 0 | parse('number') }} </td>
                              </tr>
                              <tr>
                                <td class="nowrap-table py-1 pl-0">Pembulatan</td>
                                <td class="nowrap-table py-1">:</td>
                                <td class="py-1 text-left"> Rp.&emsp;{{ modalData.employee.pembulatan_salary || 0 | parse('number') }} </td>
                              </tr>
                              <tr>
                                <td class="nowrap-table py-1 pl-0">Selisih Pembulatan</td>
                                <td class="nowrap-table py-1">:</td>
                                <td class="py-1 text-left"> Rp.&emsp;{{ modalData.employee.selisih_pembulatan || 0 | parse('number') }} </td>
                              </tr>
                              <tr v-if="modalData.employee.salary_calculated_by && modalData.employee.salary_calculated_at">
                                <td colspan="3" class="font-size-sm font-italic text-right">Dihitung Oleh {{ modalData.employee.rel_salary_calculated_by }} pada {{ modalData.employee.salary_calculated_at | parse('longDateTime') }}</td>
                              </tr>
                            </table>
                            <div class="btn-actions">
                              <button :disabled="!mainData.status_overall" @click="recountDetail()" class="btn btn-light-primary"><i class="ri-exchange-funds-line"></i> Hitung Ulang</button>
                              <button :disabled="!mainData.status_overall" v-print="printConfig" class="btn btn-light-danger"><i class="ri-printer-line"></i> Cetak PDF</button>
                              <button :disabled="!mainData.status_overall" @click="downloadSlip(selectedPeriod)" class="btn btn-light-success"><i class="ri-download-cloud-line"></i> Download</button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </b-tab>
                    <b-tab title="Kode Akun (FINA)">
                      <div class="inner-tab p-5 mt-5">
                        <div class="table-responsive">
                          <table class="table table-custom-header v-center table-bordered">
                            <template v-for="(p,i) in modalData.listTree">
                              <template v-if="p.children.length">
                                <tr :key="i+'-parent-list'">
                                  <th class="bg-light-primary" :colspan="4">{{ p.name }}</th>
                                </tr>
                                <tr v-for="(c,j) in p.children" :key="i+'-'+j+'-child-list'">
                                  <td>{{ c.rel_salary_component_id }}</td>
                                  <td class="nowrap-table text-center">
                                    <b-spinner v-if="finaAcountLoading[c.id]" label="Loading..."></b-spinner>
                                    <s-input v-else @input="updateAkunFINA(c)" v-model="c.fina_salary_account_id" :item="{ nm:true, label: null, defaultCaption:'Pilih Akun Penggajian (FINA)', type:'lookup-radio', validation:[], placeholder: '', api:'fina-salary-accounts', pointer: { label: 'account_name', code: 'id', display: ['account_code', 'account_name', 'debit_kredit|status_code'] } }" />
                                  </td>
                                </tr>
                              </template>
                            </template>
                          </table>
                        </div>
                      </div>
                    </b-tab>
                  </b-tabs>
                </b-modal>
              </template>
            </tas-base-crud>
          </b-overlay>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
moment.locale('id')
export default {
  name: 'generate-gaji',
  data () {
    return {
      selectedPeriod: null,
      isLoadingPeriod: false,
      isLoadingChecklist: false,
      isLoadingPayroll: false,
      isLoadingSlip: false,
      finaAcountLoading: {},
      mainData: {
        checklist: null,
        detailPayroll: null
      },
      config: {
        permission: {
          create: 'create-payroll-periode-calculation',
          read: 'view-payroll-periode-calculation',
          update: 'update-payroll-periode-calculation',
          delete: 'delete-payroll-periode-calculation'
        }
      },
      mainKey: 0,
      refreshKey: 0,
      configEmployee: {
        title: 'Daftar Gaji Tenaga Kerja',
        model_api: null,
        getter: 'custom/payroll-periode-calculation/employee-on-periode-list',
        setter: 'custom/payroll-periode-calculation/employee-on-periode-list',
        custom_api: {
          list: null,
          read: null
        },
        pk_field: 'employee_id',
        permission: {
          create: 'create-payroll-periode-calculation',
          read: 'view-payroll-periode-calculation',
          update: 'update-payroll-periode-calculation',
          delete: 'delete-payroll-periode-calculation'
        },
        slave: [],
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'salary_value', label: 'Penerimaan', methods: { list: { transform: 'money' } } },
          { id: 'pembulatan_salary', label: 'Pembulatan', methods: { list: { transform: 'money' } } },
          {
            id: 'employee_id',
            label: 'Nama Tenaga Kerja',
            methods: {
              list: { view_data: 'rel_employee_id' },
              detail: { view_data: 'rel_employee_id' },
              create: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              update: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              },
              filter: {
                setter: 'employees',
                getter: 'employees',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'fullname', code: 'id', display: ['nip', 'fullname']
                  }
                }
              }
            }
          },
          {
            id: 'job_position_id',
            label: 'Jabatan',
            methods: {
              list: { view_data: 'rel_job_position_id', show: false },
              detail: { view_data: 'rel_job_position_id' },
              create: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              update: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              },
              filter: {
                setter: 'job-positions',
                getter: 'job-positions',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'job_position_name', code: 'id', display: ['job_position_name']
                  }
                }
              }
            }
          },
          {
            id: 'status_employment_id',
            label: 'Status Ketenagakerjaan',
            methods: {
              list: { view_data: 'rel_status_employment_id', show: false },
              detail: { view_data: 'rel_status_employment_id' },
              create: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              },
              update: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              },
              filter: {
                setter: 'custom/master-status-employments',
                getter: 'custom/master-status-employments',
                type: 'lookup-radio',
                option: {
                  list_pointer: {
                    label: 'name', code: 'id', display: ['name']
                  },
                  get_param: [{ 'id': 'job_position_id' }]
                }
              }
            }
          },
          {
            id: 'is_mpp',
            label: 'MPP',
            methods: {
              list: {
                order: true,
                class: { false: 'badge badge-danger', true: 'badge badge-primary' },
                transform: 'ynBool'
              },
              detail: {
                order: true,
                class: { false: 'badge badge-danger', true: 'badge badge-primary' },
                transform: 'ynBool'
              },
              create: {
                type: 'radio',
                value: false,
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              },
              update: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              }
            }
          },
          {
            id: 'is_plt',
            label: 'PLT',
            methods: {
              list: {
                order: true,
                class: { false: 'badge badge-danger', true: 'badge badge-primary' },
                transform: 'ynBool'
              },
              detail: {
                order: true,
                class: { false: 'badge badge-danger', true: 'badge badge-primary' },
                transform: 'ynBool'
              },
              create: {
                type: 'radio',
                value: false,
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              },
              update: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Ya', code: true }, { label: 'Tidak', code: false }]
                  }
                }
              }
            }
          },
          {
            id: 'status_salary',
            data: 'status_salary',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              detail: {
                order: true,
                class: { 'not_calculated_yet': 'badge badge-danger', 'calculated': 'badge badge-primary' },
                transform: 'calculated'
              },
              create: false,
              update: {
                view_data: 'status_salary',
                type: 'radio',
                value: 'calculated',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              },
              filter: {
                view_data: 'status_salary',
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Sudah dihitung', code: 'calculated' }, { label: 'Belum dihitung', code: 'not_calculated_yet' }]
                  }
                }
              }
            }
          }
        ]
      },
      modalData: null,
      printConfig: {
        id: 'printSlip',
        preview: false,
        standard: 'strict'
      }
    }
  },
  watch: {
    selectedPeriod: {
      immediate: false,
      deep: true,
      handler (to, from) {
        this.$store.commit('set', ['lastSelectedPeriodPayroll', to, false])
        if (to) {
          this.$set(this.configEmployee.custom_api, 'list', 'custom/payroll-periode-calculation/employee-on-periode-list?periode_month=' + this.selectedPeriod)
          this.getChecklist()
        } else {
          this.mainData = Object.assign({}, {
            checklist: null,
            detailPayroll: null
          })
        }
      }
    },
    mainKey: {
      immediate: false,
      deep: false,
      handler (e) {
        this.$refs['sub-module'].$children[1].getData()
      }
    }
  },
  beforeMount () {
    this.getThisMonth()
  },
  mounted () {
    this.$_sys.togleClass('kt_body', 'aside-minimize', true)
    this.$store.commit('set', ['sideNavSecondary', false])
  },
  methods: {
    getRouterName (e) {
      if (e === 'payroll_periodes') return 'periode-penggajian'
      else if (e === 'employee_on_periodes') return 'periode-penggajian'
      else if (e === 'presence_on_periodes') return 'rekap-presensi'
      else if (e === 'koperasi_simpan_pinjam') return 'simpan-pinjam'
      else if (e === 'bpjs_kes_bills') return 'iuran-BPJS-kesehatan'
      else if (e === 'bpjs_tk_bills') return 'iuran-BPJS-tenaga-kerja'
      else if (e === 'tunjangan_kehadiran') return 'tunjangan-kehadiran'
      else if (e === 'tunjangan_transports') return 'tunjangan-transports'
      else if (e === 'tunjangan_makan') return 'tunjangan-makan'
      else if (e === 'tunjangan_beasiswa') return 'tunjangan-beasiswa'
      else if (e === 'tunjangan_lembur') return 'tunjangan-lembur'
      else if (e === 'pinjaman_bank') return 'pinjaman-bank'
      else if (e === 'potongan_sanksi') return 'potongan-sanksi'
      else return 'perusahaan'
    },
    redirect (module, data) {
      const routeName = this.getRouterName(module)
      if (data && data.id) this.$router.push({ name: routeName, query: { view: 'detail', id: data.id } }).catch((err) => { return err })
      else this.$router.push({ name: routeName, query: { view: 'list' } }).catch((err) => { return err })
    },
    async getThisMonth () {
      this.isLoadingPeriod = true
      const alreadySelect = this.$store.state.lastSelectedPeriodPayroll
      if (!alreadySelect) {
        let data = await this.sendAPI('list', 'payroll-periodes', { periode_month: moment(new Date()).set({ 'date': 1 }).format('YYYY-MM-DD') })
        if (data.data.length) {
          this.selectedPeriod = data.data[0].periode_month
          this.$store.commit('set', ['lastSelectedPeriodPayroll', data.data[0].periode_month, false])
        }
      } else this.selectedPeriod = alreadySelect
      this.isLoadingPeriod = false
    },
    async getChecklist () {
      this.isLoadingChecklist = true
      let data = await this.sendAPI('get', 'custom/payroll-periode-calculation/checklist', { periode_month: this.selectedPeriod })
      this.$set(this.mainData, 'checklist', data.data)
      this.$set(this.mainData, 'status_overall', data.status_overall)
      this.getDetailPayroll()
      this.isLoadingChecklist = false
    },
    async getDetailPayroll () {
      this.isLoadingPayroll = true
      let data = await this.sendAPI('get', 'custom/payroll-periode-calculation/payroll-periode-show', { periode_month: this.selectedPeriod })
      this.$set(this.mainData, 'detailPayroll', data.data)
      this.isLoadingPayroll = false
    },
    sendAPI (method, api, param) {
      return this.$_api[method](api, param).then(res => {
        return res
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    async calculateAllEmployee () {
      this.isLoadingPayroll = true
      let data = await this.sendAPI('post', 'custom/payroll-periode-calculation/salary-calculation-all', { periode_month: this.selectedPeriod })
      if (data) this.$_alert.success('Berhasil menghitung semua Gaji Tenaga Kerja')
      this.mainKey++
      this.isLoadingPayroll = false
    },
    async getPayrollDetail (id, data) {
      const listTree = await this.sendAPI('get', 'custom/payroll-periode-calculation/salary-component-on-periodes-list-tree', { id: data.id })
      const detail = {
        listTree: listTree.data.data_salary_detail,
        presence: listTree.data.data_employee_presence_recaps,
        employee: listTree.data.data_employee
      }
      this.modalData = Object.assign({}, detail, data)
      this.$nextTick().then(e => {
        this.$bvModal.show(id)
      })
    },
    async downloadSlip (e) {
      this.isLoadingSlip = true
      var domtoimage = require('dom-to-image-more')
      const capture = document.getElementById('printSlip')
      await domtoimage.toPng(capture).then(function (url) {
        const link = document.createElement('a')
        link.href = url
        const filename = 'Slip Gaji Periode ' + moment(e).format('MMMM YYYY')
        link.setAttribute('download', filename + '.png') // or any other extension
        document.body.appendChild(link)
        link.click()
      })
      this.isLoadingSlip = false
    },
    async updateAkunFINA (e) {
      this.$set(this.finaAcountLoading, e.id, true)
      let formData = {
        id: e.id,
        fina_salary_account_id: e.fina_salary_account_id
      }
      await this.sendAPI('put', 'custom/payroll-periode-calculation/salary-component-on-periodes-account-update', formData)
      this.$set(this.finaAcountLoading, e.id, false)
    },
    async recountDetail () {
      this.isLoadingSlip = true
      let formData = {
        id: this.modalData.id,
        periode_month: this.selectedPeriod
      }
      const result = await this.sendAPI('post', 'custom/payroll-periode-calculation/salary-calculation-single', formData)
      if (result.check) {
        const listTree = await this.sendAPI('get', 'custom/payroll-periode-calculation/salary-component-on-periodes-list-tree', { id: this.modalData.id })
        const detail = {
          listTree: listTree.data.data_salary_detail,
          presence: listTree.data.data_employee_presence_recaps,
          employee: listTree.data.data_employee
        }
        this.modalData = Object.assign(this.modalData, detail)
        this.mainKey++
      }
      this.isLoadingSlip = false
    },
    lockDocument (data) {
      // const form = {
      //   periode_month: this.selectedPeriod
      // }
      if (data.is_lock === true) {
        this.$_alert.confirm('Kunci Perhitungan', 'Data Penggajian tidak dapat diubah kembali ketika perhitungan Gaji dikunci, Lanjutkan?').then((result) => {
          if (result.isConfirmed) {
            // this.$_api.post('custom/control-sheet-payroll/submit-lock', form).then(res => {
            //   this.$_alert.success('Berhasil mengunci Perhitungan')
            //   data.is_lock = false
            // })
            this.$router.push({ name: 'control-sheet', query: { view: 'list' } }).catch((err) => { return err })
          } else data.is_lock = false
        })
      } else {
        this.$_alert.confirm('Buka Kunci Perhitungan', 'Data Penggajian tidak dapat diubah kembali ketika perhitungan Gaji dikunci, Lanjutkan?').then((result) => {
          if (result.isConfirmed) {
            // this.$_api.post('custom/control-sheet-payroll/submit-lock', form).then(res => {
            //   this.$_alert.success('Berhasil mengunci Perhitungan')
            //   data.is_lock = true
            // })
          } else data.is_lock = true
        })
      }
    }
  }
}
</script>

<style scoped>
  .arrow-info-inside {
    cursor: pointer;
  }
  .arrow-info-inside .icon-arrow {
    transition: all 0.5s ease;
    padding-left: 0.25rem;
  }
  .arrow-info-inside:hover .icon-arrow {
    padding-left: 0.8rem;
    color: var(--primary);
  }
  .timeline-badge.custom-badge {
    width: 15px !important;
    height: 15px !important;
  }
  .timeline-bar.custom-bar {
    left: 6.5px !important;
    width: 2px !important;
  }
  .scrolling-view {
    padding-right: 1rem;
    max-height: 80vh;
    overflow-y: auto;
  }
  .switch.switch-icon.icon-lock input:checked ~ span:after {
    font-family: 'remixicon' !important;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    content: '\EECB';
    font-size: 12px;
  }
</style>



